        keys: "Version,URL,-createdAt,-updatedAt,-objectId",
      };
      const url = `https://3dec7zyj.lc-cn-n1-shared.com/1.1/classes/Update/674aa11c7f88791bd117bf88?${new URLSearchParams(
        params
      )}`;
      const response = await fetch(url, options);
      this.update = await response.json();
      this.version = this.update.Version;
      this.url = this.update.URL;
    },
  },
};
